<script>
/////////////////////////////////////////////////////

import PhotoThumbnail from '@/components/photos/PhotoThumbnail.vue';
import { FluroInfiniteScroll, FluroPagedContent } from 'fluro-vue-ui'
/////////////////////////////////////////////////////

import BreadcrumbMixin from '@/mixins/BreadcrumbMixin'
import PhotoBrowseMixin from '@/mixins/PhotoBrowseMixin'


export default {
    props: {
        source: [String, Object],
    },
    mixins: [BreadcrumbMixin, PhotoBrowseMixin],
    components: {
        FluroInfiniteScroll,
        FluroPagedContent,
        PhotoThumbnail,
    },
    data() {
        return {
            loading: true,
            search: '',
            searchFocussed: false,
            debouncedSearch: this.search,
        }
    },
    computed: {
        routeKey() {
            return this.$route.name.split('.')[0];
        }
    },
    methods: {
        view(photo) {
            var photoID = this.$fluro.utils.getStringID(photo);
            this.$router.push({ query: { photo: photoID } });
        },
    },
    created() {},
    watch: {
        search: _.debounce(function(newValue) {
            this.debouncedSearch = newValue;
            // this.refine();
        }, 500),
    },
    computed: {
        sourceItem() {
            return this.meta.item;
        },
        photos() {
            return this.meta.photos;
        },
        sorted() {
            return this.filtered;

            // var self = this;
            //  return _.orderBy(self.filtered, function(item) {
            //     return String(item.title).toLowerCase();
            // });
        },
        filtered() {



            var self = this;
            var filtered = self.photos;

            if (!self.debouncedSearch || !self.debouncedSearch.length) {
                return filtered;
            }

            ////////////////////////////////////////////////

            //Get the search keywords
            var searchKeywords = String(self.debouncedSearch).toLowerCase().trim();
            var searchPieces = searchKeywords.split(' ');

            ////////////////////////////////////////////////

            return _.filter(filtered, function(item) {

                var itemTitle = String(item.title).trim().toLowerCase();

                var exactMatch = _.includes(itemTitle, searchKeywords);
                if (exactMatch) {
                    return true;
                }

                var pieces = itemTitle.split(' ');
                pieces = pieces.concat(item.keywords);


                var match = _.every(searchPieces, function(partial) {
                    return _.some(pieces, function(word) {
                        return _.includes(word, partial);
                    })
                })

                if (match) {
                    return true;
                }
            });


        }
    },
    asyncComputed: {
        meta: {
            default: {},
            get() {

                var self = this;

                var source = self.$fluro.utils.getStringID(self.source);


                self.loading = true;

                return new Promise(function(resolve, reject) {

                    self.$fluro.api.get(`/photos/realm/${source}`, {
                        params: {
                            // populate:[
                            //     'data.event',
                            //     'data.photographer',
                            // ],
                            verbose: true,
                            simple: 'false',
                            select: [
                                'title',
                                'managedAuthor',
                                'created',
                                'data.photographer',
                                'data.event',
                                'width',
                                'colors',
                                'height',
                            ],
                            limit: 10000,
                        }
                    }).then(function(res) {
                        resolve(res.data);
                        self.loading = false;
                    }, function(err) {
                        reject(err);
                        self.loading = false;
                    })
                });
            }
        }

    },
}
</script>
